<template>
  <div class="chat-index">
    <template v-for="(item, i) in list">
      <chat-item :item="item" :key="i" @toChat="handleToChat(item)"></chat-item>
    </template>
  </div>
</template>
<script>
import ChatItem from '@/components/ChatItem'
export default {
  name: 'GroupChat',
  components: { ChatItem },
  data () {
    const user = localStorage.getItem('user')
    return {
      user: (user && JSON.parse(user)) || {},
      list: []
    }
  },
  methods: {
    handleToChat (item) {
      if (item.chatType === 1) {
        this.$router.push('/groupChat')
        localStorage.setItem('chatItem', JSON.stringify(item))
      } else {
        this.$router.push('/friendChat')
        localStorage.setItem('friendChatItem', JSON.stringify(item))
      }
    }
  },
  created () {
    this.$socket.chats(this.user.id, (res) => {
      this.list = res.chats
    })
  }
}
</script>
<style lang="less">
  .chat-index {
    background: #fff;
  }
</style>
