<template>
  <div class="chat-item my-border" @click="$emit('toChat')">
    <div class="list-img">
      <img :src="item.imgUrl" alt="" />
      <template v-if="item.unreadNumber !== 0">
        <mt-badge size="small" type="error" color="red">{{getNum(item.unreadNumber)}}</mt-badge>
      </template>
    </div>
    <div class="right-box">
      <div class="right-box-top">
        <span class="name">{{item.chatName}}</span>
        <span class="time">{{item.openTime}}</span>
      </div>
      <div class="right-box-bot">{{item.content}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ChatItem',
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    getNum (num) {
      return num <= 100 ? num : '99+'
    }
  }
}
</script>
<style lang="less">
  .chat-item {
    height: 80px;
    padding: 16px;
    display: flex;
    align-items: center;
    .list-img {
      position: relative;
      margin-right: 12px;
      img {
        width: 52px;
        height: 52px;
        border-radius: 5px;
      }
      .mint-badge {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(50%, -50%);
        padding: 0 6px;
      }
    }
    .right-box {
      width: calc(100% - 64px);
      height: 52px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .right-box-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .name {
          font-size: 15px;
          color: #000;
        }
        .time {
          font-size: 12px;
          color: #B6B6B6;
        }
      }
      .right-box-bot {
        text-align: left;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #B6B6B6;
        font-size: 12px;
      }
    }
  }
</style>
